@import 'src/scss/settings/mixins';
@import 'src/scss/settings/colors';

.aboutUsContainer {
  width: 100%;
  height: 90rem;
  display: flex;
  align-items: center;
  flex-direction: column;
  background-image: url('../../../assets/bgAboutUs.jpg');
  background-repeat: no-repeat;
  background-size: 120%;
  background-position: calc(100%);
  .title {
    @include title;
    color: $secondFontColorDark;
  }
  .tailsContainer {
    width: 75%;
    height: 70%;
    margin: 5% 0 5% 0;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    flex-wrap: wrap;
  }
}
@include desktop {
  .aboutUsContainer {
    height: 100rem;
    background-size: cover;
    background-position: center;
  }
}
@include tabletMax {
  .aboutUsContainer {
    height: 90rem;
    .tailsContainer {
      width: 95%;
    }
  }
}
@include tabletMin {
  .aboutUsContainer {
    height: 100rem;
  }
}
@include mobile {
  .aboutUsContainer {
    height: 140rem;
  }
}
